import { ComponentChildren } from 'preact';
import { borderInterpolations } from '../utils/UNSAFE_interpolations/borders';
import type { BorderProps } from '../utils/UNSAFE_interpolations/borders';
import { dimensionInterpolations } from '../utils/UNSAFE_interpolations/dimensions';
import type { DimensionProps } from '../utils/UNSAFE_interpolations/dimensions';
import { flexitemInterpolations } from '../utils/UNSAFE_interpolations/flexitem';
import type { FlexitemProps } from '../utils/UNSAFE_interpolations/flexitem';
import { colorInterpolations } from '../utils/UNSAFE_interpolations/colors';
import type { ColorProps } from '../utils/UNSAFE_interpolations/colors';
import { paddingInterpolations } from '../utils/UNSAFE_interpolations/padding';
import type { PaddingProps } from '../utils/UNSAFE_interpolations/padding';
import { ariaInterpolations } from '../utils/UNSAFE_interpolations/aria';
import type { AriaProps } from '../utils/UNSAFE_interpolations/aria';
import { mergeInterpolations } from '../utils/UNSAFE_mergeInterpolations';
import { stringLiteralArray } from '../utils/UNSAFE_arrayUtils';
import type { Role } from '../utils/UNSAFE_roles';

const elementTypes = stringLiteralArray(['div', 'main', 'article', 'section', 'aside']);

type ElementType = typeof elementTypes[number];

type StyleProps = BorderProps &
  DimensionProps &
  FlexitemProps &
  ColorProps &
  PaddingProps &
  AriaProps;

type Props = StyleProps & {
  as?: ElementType;
  labelledBy?: string;
  role?: Role;
  children?: ComponentChildren;
};

const interpolations = [
  ...Object.values(borderInterpolations),
  ...Object.values(dimensionInterpolations),
  ...Object.values(flexitemInterpolations),
  ...Object.values(colorInterpolations),
  ...Object.values(paddingInterpolations),
  ...Object.values(ariaInterpolations)
];

const styleInterpolations = mergeInterpolations<Props>(interpolations);

export const View = ({ as, children, ...props }: Props) => {
  const { class: cls, ...styles } = styleInterpolations(props);
  // we hardcode box-sizing: border-box on all of our Preact components.
  styles['boxSizing'] = 'border-box';
  const Component = as || 'div';
  return (
    <Component class={cls} style={styles}>
      {children}
    </Component>
  );
};

export { elementTypes };
